import React from 'react';
import './App.css';
import {placeMISBuyLimitOrder,placeMISSellLimitOrder,placeMISBuyMarketOrder,placeMISSellMarketOrder,getPosition} from './OrderUtil';



class BasketOrderDialog extends React.Component {
	  constructor(props) {
	    super(props);  

      let orderWindowView = {buyView:true,sellView:true};
      orderWindowView = localStorage.getItem("basketOrderWindowView")!=undefined ?JSON.parse(localStorage.getItem("basketOrderWindowView")):orderWindowView;      

      this.rowfRefs ={};

      this.state={orderWindowView:orderWindowView,isHideDialog:false,isMarketOrder:true};    

	  }

  toogleBuyView=()=>{
    let orderWindowView = this.state.orderWindowView;
    orderWindowView.buyView=!orderWindowView.buyView;
    this.setState({orderWindowView:orderWindowView});

    localStorage.setItem("basketOrderWindowView", JSON.stringify(orderWindowView));    
  }


  toogleSellView=()=>{
    let orderWindowView = this.state.orderWindowView;
    orderWindowView.sellView=!orderWindowView.sellView;
    this.setState({orderWindowView:orderWindowView});

    localStorage.setItem("basketOrderWindowView", JSON.stringify(orderWindowView));  
  }

  onPositionSuccess=(data)=>{
    this.setState({positionsdata:data})
  }
  onPositionError=(data)=>{
   //this.setState({positionsdata:undefined})
  }

  fetchPositionData=(data)=>{
    getPosition(this.onPositionSuccess,this.onPositionError);
  }

  componentDidMount() {
    this.fetchPositionData();
    this.interval = setInterval(this.fetchPositionData, 2000);
  }

  componentWillUnmount(){
    clearInterval( this.interval );
  }

	componentDidUpdate(prevProps) {

	}

	updateOrderStatus=(data)=>{
		this.setState({orderStatus:data})
	}

 showDialog=(data)=>{
    this.setState({isHideDialog:false})
  }


placeBuyOrderForselectedRow=()=>{
  let isOk = window.confirm("BUY order for all selected rows confirm");
  if(isOk){
      for (let key of Object.keys(this.rowfRefs)) {
        let rowRef  = this.rowfRefs[key];
        if(rowRef.isCheckBoxChecked()){
          rowRef.sendBuyOrderRequest();
        } 
    }
  }

}


 placeSellOrderForselectedRow=(data)=>{
  let isOk = window.confirm("SELL order for all selected rows confirm");
  if(isOk){
    for (let key of Object.keys(this.rowfRefs)) {
        let rowRef  = this.rowfRefs[key];
        if(rowRef.isCheckBoxChecked()){
          rowRef.sendSellOrderRequest();
        } 
    }    
  }
}


onSQOFFClick=()=>{
  const perc = window.prompt('Percetage to SQ OFF')
  let totalValue=0;
  if(perc!=null && perc!="" && !isNaN(perc) ){
    for (let key of Object.keys(this.rowfRefs)) {
        let rowRef  = this.rowfRefs[key];
        totalValue+=rowRef.fillQtyValueOnSqOffClick(perc);
    }
  }
  this.updateOrderStatus('Order Value -> '+ (totalValue/1000).toFixed(1)+"K");

}


fillQtyForValue=()=>{
  const value = window.prompt('Build Position for amount')
  let totalValue=0;
  if(value!=null && value!="" && !isNaN(value) ){
    for (let key of Object.keys(this.rowfRefs)) {
        let rowRef  = this.rowfRefs[key];
        if(rowRef.isCheckBoxChecked()){
          totalValue+=rowRef.fillQtyForValue(value);
        }         
    }
    this.updateOrderStatus('Order Value -> '+ (totalValue/1000).toFixed(1)+"K");

  }

}



  render() {

    let{
      kiteBasketOrderData,
    }=this.props;

    const{
      kitedata,
      hideBasketOrderDialog,
      tableId,
      addToFavList,
      removeFromFavList,
      onTableRowclick,
//      positionsdata,  //poll from server 
      updateMarginData
    }=this.props;

    const{
    	orderStatus,
      orderWindowView,
      isHideDialog,
      positionsdata, //poll from client 
      isMarketOrder,
      isSortPosition
    }=this.state;


    let positionMap={};
    if(positionsdata){

      if(isSortPosition){
            positionsdata.data.net.sort(function(a, b){
              if(a.product == 'MIS' && b.product=='MIS'){
                if(a.quantity != 0 && b.quantity != 0) return a.pnl - b.pnl;
                else return b.quantity - a.quantity;    
              }else if(a.product == 'MIS' && b.product=='CNC'){
                 return -1; 
              }else if(a.product == 'CNC' && b.product=='MIS'){
                 return 1; 
              }else{
                return 0;
              }
            });

      }      


      for(let i=0;i<positionsdata.data.net.length;i++){
        if(positionsdata.data.net[i].product == 'MIS' && kitedata.ticks[positionsdata.data.net[i].tradingsymbol]!=undefined){
          positionMap[positionsdata.data.net[i].tradingsymbol]=positionsdata.data.net[i];
        }      
      }

     if(isSortPosition){      
         for(let i=0;i<kiteBasketOrderData.length;i++){
            if( positionMap[kiteBasketOrderData[i]]==undefined){
              positionMap[kiteBasketOrderData[i]]=undefined;
            }       
         } 

         kiteBasketOrderData = Object.keys(positionMap);
      }   
      

    }


	 	
	    return (
	      <div id='basketOrderDialog' style={{display:(isHideDialog?'none':'block')}} className="modal">       
	        <div style={{width:'830px'}} className="modal-content">
	          <div  className="orderStatusView">{orderStatus}</div>	
	          <span onClick={hideBasketOrderDialog} className="close">&times;</span>
	          <div className='basket-orders'>
	          
          <table>
            <thead>
              <tr>
                <th>Select</th>
                <th>Name</th>
                <th>PnL</th>
                <th>Change</th>
                <th>LTP</th>
                <th>Qty</th>
                <th>Price</th>
             {orderWindowView.buyView &&  <th>BUY</th>		}		 
				     {orderWindowView.sellView &&    <th>SELL</th> }
              </tr>
            </thead>

            <tbody>
            
             { kiteBasketOrderData.map((symbol,index)=> <BasketListRow fetchPositionData={this.fetchPositionData} updateMarginData={updateMarginData} positionsdata={positionMap[symbol]} ref={ele => this.rowfRefs['MIS-'+symbol] = ele} isMarketOrder={isMarketOrder} orderWindowView={orderWindowView} onTableRowclick={onTableRowclick} addToFavList={addToFavList} removeFromFavList={removeFromFavList} tableId={tableId} updateOrderStatus={this.updateOrderStatus} key={'MIS-'+symbol} kitedata={kitedata}  symbol={symbol} />)}
             
            </tbody>


        </table>

	         </div>

        <button className="favButton"  onClick={this.toogleSellView} type="button">SW</button>
        <button className="favButton"  onClick={this.toogleBuyView} type="button">BW</button>

        <button className="favButton"  onClick={(evt)=>{this.setState({isSortPosition:!isSortPosition})}} type="button">{isSortPosition?'Orignal':'Sort'}</button>

        <button className="favButton"  onClick={(evt)=>{this.setState({isHideDialog:true})}} type="button">Hide</button>

        <button className="favButton"  onClick={this.onSQOFFClick} type="button">SQFF</button>

        <button className="favButton"  onClick={this.fillQtyForValue} type="button">Fill Qty</button>

        <button  style={{color:'red'}} className="favButton"  onClick={(evt)=>{this.setState({isMarketOrder:!isMarketOrder})}} type="button">{isMarketOrder?'MARKET':'LIMIT'}</button>


        {orderWindowView.buyView && <button style={{marginLeft: '50px'}} className="favButton buyButton"  onClick={this.placeBuyOrderForselectedRow} type="button">Buy</button> }
        {orderWindowView.sellView &&    <button style={{marginLeft: '50px'}} className="favButton sellButton"  onClick={this.placeSellOrderForselectedRow} type="button">Sell</button> }



	       </div>
	      </div>
	    )
  }

}


export default BasketOrderDialog;




class BasketListRow extends React.Component {
  constructor(props) {
    super(props);
    this.state={isRowEnable:false};
  }


	componentDidMount() {
		this.fillInputWithLatestPrice();
	}  

	componentDidUpdate(prevProps) {

	}



  fillInputWithLatestPrice=()=>{
    const{
      kitedata,
      symbol
    }=this.props;
  	let depth = kitedata.ticks[symbol].depth;	

  	if(depth){
	  	this.setState({quantity:0,price:kitedata.ticks[symbol].lastTradedPrice});
  	}

  }


  fillQtyForValue=(value)=>{
    const{
      kitedata,
      symbol
    }=this.props;
    let lastTradedPrice = kitedata.ticks[symbol].lastTradedPrice; 
    let quantity =  Number.parseInt(value/lastTradedPrice);
    this.setState({quantity:quantity});
    return quantity*lastTradedPrice;
  }


  onInputValueChange=(evt)=>{

    let{
      quantity,
      price,
      isRowEnable
    }=this.state;

  	if(evt.target.name == 'quantity'){
  		quantity=evt.target.value;
  		this.setState({quantity:quantity});
  	}else if(evt.target.name == 'price'){
  		 price=evt.target.value;
  		this.setState({price:price});
  	}else if(evt.target.name == 'checkbox'){
      isRowEnable=evt.target.checked;
      this.setState({isRowEnable:isRowEnable});      
    }

  }


 fillQtyValueOnSqOffClick=(perc)=>{

    let{
      positionsdata,
      kitedata,
      symbol      
    }=this.props;

    let orderValue =0;
    if(positionsdata){
      let quantity = Number.parseInt(Math.abs((positionsdata.quantity*perc/100)));      
      this.setState({quantity:quantity});
      if(quantity==0){
        this.markCheckBox(false);
      }else{
        this.markCheckBox(true)
      }

      let lastTradedPrice = kitedata.ticks[symbol].lastTradedPrice;
      orderValue = quantity*lastTradedPrice;
    }

    return orderValue;
  }

onOrderSuccess=(data)=>{
    const{
      updateOrderStatus,
      updateMarginData,
      fetchPositionData
    }=this.props;		
    updateMarginData();
    fetchPositionData();
	updateOrderStatus(data.data.order_id);

	this.setState({quantity:0,isRowEnable:false});
  this.markCheckBox(false);
}


onOrderError=(data)=>{
    const{
      updateOrderStatus,
      updateMarginData,
      fetchPositionData
    }=this.props;		
    updateMarginData();
    fetchPositionData();
	updateOrderStatus(data.message);
	this.setState({quantity:0,isRowEnable:false});
  this.markCheckBox(false);
}  

markCheckBox=(enable)=>{
 // this.checkBoxRef.checked=enable;
  this.setState({isRowEnable:enable});
}  

isCheckBoxChecked=()=>{
  return this.state.isRowEnable == true;
}  

getOrderValue=()=>{

    const{
      quantity,
      price,
    }=this.state;

  return quantity * price;
}  

sendBuyOrderRequest=()=>{

    const{
      quantity,
      price,
    }=this.state;

    const{
      kitedata,
      symbol,
      updateOrderStatus,
      isMarketOrder
    }=this.props;

   if(isMarketOrder){
      placeMISBuyMarketOrder({exchange:kitedata.ticks[symbol].exchange,tradingsymbol:symbol,price:price,quantity:quantity,transaction_type:'BUY',order_type:'MARKET'},this.onOrderSuccess,this.onOrderError);
      updateOrderStatus(symbol +' -> BUY  -> MARKET -> MIS ->' +'        Q: '+ quantity + '   P: '+price);
   } else{
      placeMISBuyLimitOrder({exchange:kitedata.ticks[symbol].exchange,tradingsymbol:symbol,price:price,quantity:quantity,transaction_type:'BUY',order_type:'LIMIT'},this.onOrderSuccess,this.onOrderError);
      updateOrderStatus(symbol +' -> BUY  -> LIMIT -> MIS ->' +'        Q: '+ quantity + '   P: '+price);    
   }


}

sendSellOrderRequest=()=>{

    const{
      quantity,
      price,
    }=this.state;

    const{
      kitedata,
      symbol,
      updateOrderStatus,
      isMarketOrder
    }=this.props;

   if(isMarketOrder){
      placeMISSellMarketOrder({exchange:kitedata.ticks[symbol].exchange,tradingsymbol:symbol,price:price,quantity:quantity,transaction_type:'SELL',order_type:'MARKET'},this.onOrderSuccess,this.onOrderError);
      updateOrderStatus(symbol +' -> SELL  -> MARKET -> MIS ->' +'        Q: '+ quantity + '   P: '+price);    
   }else{
      placeMISSellLimitOrder({exchange:kitedata.ticks[symbol].exchange,tradingsymbol:symbol,price:price,quantity:quantity,transaction_type:'SELL',order_type:'LIMIT'},this.onOrderSuccess,this.onOrderError);
      updateOrderStatus(symbol +' -> SELL  -> LIMIT -> MIS ->' +'        Q: '+ quantity + '   P: '+price);    
   }

}


  openStockInfo=()=>{
    this.props.onTableRowclick(this.props.symbol);
  }

  render() {

    const{
      kitedata,
      symbol,
      tableId,
      addToFavList,
      removeFromFavList,
      onTableRowclick, 
      orderWindowView,
      positionsdata     
    }=this.props;


    let{
      quantity,
      price,
      isRowEnable
    }=this.state;


    let changeClass  = '';
    if( kitedata.ticks[symbol] ){
      changeClass=kitedata.ticks[symbol].change<0 ?'red':'green';
    }

    let checkboxstyle={};
    if(isRowEnable){
      checkboxstyle.backgroundColor='grey';
    } 
    if(positionsdata && positionsdata.quantity > 0){
      checkboxstyle.borderLeft='20px solid lightgreen';
    }else if(positionsdata && positionsdata.quantity < 0){
      checkboxstyle.borderLeft='20px solid red';
    }else {
      checkboxstyle.borderLeft='20px solid white';
    }     

    //ref={ele => this.checkBoxRef = ele}

    return (
      <tr>
      <td onClick={(evt)=>{this.setState({isRowEnable:!isRowEnable})}} style={checkboxstyle}><input style={{width:'20px',height: '20px'}}    onChange={this.onInputValueChange} type="checkbox" name='checkbox' checked={isRowEnable}></input></td>  
			<td onDoubleClick={(evt)=>{addToFavList(symbol,'MIS');}} >{symbol}<span className='exchangeSuffix'>&nbsp;&nbsp;{kitedata.ticks[symbol].exchange}</span> &nbsp;{positionsdata&&positionsdata.quantity!=0 && <span><span className={'holdQty ' + (positionsdata.quantity<0?'red':'green')}>({positionsdata.quantity})</span>&nbsp;&nbsp;<span className='positionValue'>{Math.round((positionsdata.value)/1000) }k</span></span>}</td>    
			<td className={positionsdata && positionsdata.pnl<0?'red':'green'}>{positionsdata ?positionsdata.pnl.toFixed(1):'' }</td>   
      <td onClick={this.openStockInfo} className={changeClass}>{(kitedata.ticks[symbol].change).toFixed(2)}%</td>    
			<td >{kitedata.ticks[symbol].lastTradedPrice}</td>    
			<td>
				<input type="number"   autoComplete="off" onClick={event => event.target.select()} onInput={this.onInputValueChange} className="quantity" step={1} name="quantity" min="1" max="1000" value={quantity}></input>
			</td>         
			<td>
				<input type="number"  autoComplete="off" onClick={event => event.target.select()}   onInput={this.onInputValueChange} className="price" step={0.2} name="price" min="1" max="10000" value={price}></input>
			</td> 		

			{orderWindowView.buyView && <td><button  className = 'buyButton' onClick={this.sendBuyOrderRequest}> BUY </button></td> }
			{orderWindowView.sellView && <td> <button style={{backgroundColor:'#e86666'}} className='sellButton' onClick={this.sendSellOrderRequest}> SELL </button></td>}
      
       </tr>
    );


  }

}